import React from 'react'
import Scrollspy from 'react-scrollspy'
import ReactAudioPlayer from 'react-audio-player';

import {
	Collapse,
	Navbar,
	NavbarToggler,
	NavbarBrand,
	NavItem,
	NavLink,
} from 'reactstrap';


export default class NavigationMenu extends React.Component {
	constructor(props) {
		super(props);

		this.toggle = this.toggle.bind(this);
		this.state = {
			isOpen: false
		};
	}
	toggle() {
		this.setState({
			isOpen: !this.state.isOpen
		});
	}

	render() {
		return (
			<>
				<Navbar className="fixed-top" dark expand="md">
					<div className="container">
						<NavbarBrand href="/" className="d-block d-md-none mr-auto">AG</NavbarBrand>
						<NavbarToggler onClick={this.toggle} className="mr-2" aria-label="Toogle navigation menu" />
						<Collapse isOpen={this.state.isOpen} navbar>
							<Scrollspy className="mr-auto navbar-nav" items={['presentation', 'formation', 'competences', 'experiences', 'realisations', 'contact']} currentClassName="active">
								<NavItem onClick={this.toggle}>
									<NavLink href="/#presentation">
										<i className="fa fa-home" aria-hidden="true"></i>
									</NavLink>
								</NavItem>
								<NavItem onClick={this.toggle}>
									<NavLink href="/#formation">Formation</NavLink>
								</NavItem>
								<NavItem>
									<NavLink onClick={this.toggle} href="/#competences">Compétences</NavLink>
								</NavItem>
								<NavItem onClick={this.toggle}>
									<NavLink href="/#experiences">Expériences</NavLink>
								</NavItem>
								<NavItem onClick={this.toggle}>
									<NavLink href="/#realisations">Réalisations</NavLink>
								</NavItem>
								<NavItem onClick={this.toggle}>
									<NavLink href="/#contact">Contact</NavLink>
								</NavItem>
							</Scrollspy>
						</Collapse>
					</div>
				</Navbar>
				<ReactAudioPlayer
					className="audio-player"
					src="Ever Felt pt. 2 - Otis McDonald.mp3"
					autoPlay
					controls
				/>
		  </>
		);
	}
}